import React from 'react'
import { Formik, Form, Field } from 'formik'
import { TextField, SelectField, ArrayField } from '../../utils/FormFields'
import { Button } from 'semantic-ui-react'
import { NewUser, OptionField } from '../../types'

interface Props {
    onSubmit: (values: NewUser) => void;
}

const initialValues: NewUser = {
    name: "",
    username: "",
    password: "",
    type: "Customer",
    languages: [""]
}
const options: OptionField[] = [
    {label: "Asiakas", value: "Customer"},
    {label: "Yllpitj", value: "Admin"},
    {label: "Opas", value: "Guide"},
]

export const RegisterationForm: React.FC<Props> = ({ onSubmit }) => {
    return (
        <Formik
        initialValues={initialValues}
        onSubmit={(values, { resetForm }) => {
            onSubmit(values)
            resetForm()
        }}
        validate={ values => {
            const errors: { [field: string]: string | object} = {};
            if(values.username.length === 0) {
                errors.username = "Kentt vaaditaan"
            }
            if(values.password.length === 0) {
                errors.password = "Kentt vaaditaan"
            }
            if(values.name.length === 0) {
                errors.name = "Kentt vaaditaan"
            }
            if(values.type !== "Admin" && values.type !== "Customer" && values.type !== "Guide") {
                errors.type = "Kyttjn tytyy kuulua johonkin ennalta valittuun ryhmn"
            }
            return errors;
        }}
        >
        {({ values, isValid, dirty }) => {
            return(
            <Form className="form ui">
                <Field
                    label="Nimi"
                    placeholder="Nimi"
                    name="name"
                    component={TextField}
                />
                <Field
                    label="Kyttjnimi"
                    placeholder="Kyttj"
                    name="username"
                    component={TextField}
                />
                <Field
                    label="Salasana"
                    placeholder="Salasana"
                    name="password"
                    component={TextField}
                />
                <SelectField
                    label="Kyttjtyyppi"
                    name="type"
                    options={options}
                />

                {values.type === "Guide" && 
                    <Field
                        label="Kielitaito"
                        name="languages"
                        component={ArrayField}
                        values={values.languages}
                        fieldName="languages"
                    />
                }
                <Button id="registerationButton" type="submit" name="submit" disabled={!dirty || !isValid}>
                        Lis kyttj
                </Button>
            </Form>
            )
        }}
        </Formik>
    )
}